import styled from 'styled-components';

const BackgroundStyled = styled.div`
  .background-img {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100vw;
  }
  .background-img-darken {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    top: 0;
    background: rgba(0, 0, 0, 0.8);
    height: 100vh;
    width: 100vw;
  }
`;

export default BackgroundStyled;
